import "./styles.css";
import "primereact/resources/primereact.min.css";
import "primereact/resources/themes/bootstrap4-dark-blue/theme.css";
import "primeicons/primeicons.css";
import "primeflex/primeflex.css";

import { Dropdown } from "primereact/dropdown";
import { MultiSelect } from "primereact/multiselect";
import { Spacer } from "./Spacer";
import { useState } from "react";

const options = Array(100)
  .fill()
  .map((_, i) => {
    return { id: i, label: `Option ${i}` };
  });

export default function App() {
  const [single, setSingle] = useState(null);
  const [multiple, setMultiple] = useState([]);

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2>
      <Spacer />
      <h2>Here is the thing</h2>
      <label>
        <p>Example for Dropdown component</p>
        <Dropdown
          options={options}
          placeholder="choose one option"
          value={single}
          onChange={({ value }) => setSingle(value)}
        />
      </label>
      <label>
        <p>Example for MultiSelect component</p>
        <MultiSelect
          className="col-12"
          options={options}
          placeholder="choose one or more options"
          value={multiple}
          onChange={({ value }) => setMultiple(value)}
        />
      </label>
      <h2>Some more space</h2>
      <Spacer />
    </div>
  );
}
